import { environment } from 'src/environments/environment';
import { LoadingService } from 'src/app/shared/services/loading.service';
import { Component, OnInit } from '@angular/core';

declare const L: any;
import 'leaflet';
import 'leaflet-draw';
import * as turf from '@turf/turf';
import { ToastrService } from 'ngx-toastr';
import { HttpService } from 'src/app/shared/services/http.service';
import { Response } from '@angular/http';
import { PolygonAppService } from 'src/app/shared/services/polygon-app.service';

@Component({
  selector: 'app-dashboard-map',
  templateUrl: './dashboard-map.component.html',
  styleUrls: ['./dashboard-map.component.scss']
})
export class DashboardMapComponent implements OnInit {
  isCreating = false;
  map: any;
  zoomDefault = 15;
  lat = 4.5230495; //4.8024814;
  lng = -74.25125616666666; //-74.094631;

  polygons: any[] = [];
  polygonDrawer: any;
  newRiskPolygon: any;

  MapaHibrido = L.tileLayer('http://mt0.google.com/vt/lyrs=y&hl=en&x={x}&y={y}&z={z}', {id: 'MapID', tileSize: 512, zoomOffset: -1, attribution: 'Google'});
  MapaCarretera   = L.tileLayer("http://mt0.google.com/vt/lyrs=m&hl=en&x={x}&y={y}&z={z}  ", {id: 'MapID', tileSize: 512, zoomOffset: -1, attribution: 'Google'});
  MapaTerranea   = L.tileLayer("http://mt0.google.com/vt/lyrs=p&hl=en&x={x}&y={y}&z={z} ", {id: 'MapID', tileSize: 512, zoomOffset: -1, attribution: 'Google'});
  MapaSatelite   = L.tileLayer("http://mt0.google.com/vt/lyrs=s&hl=en&x={x}&y={y}&z={z} ", {id: 'MapID', tileSize: 512, zoomOffset: -1, attribution: 'Google'});

  constructor(private httpService: HttpService,
              private loadingService: LoadingService,
              private toastr: ToastrService,
              private polygonService: PolygonAppService,) {}

  ngOnInit() {
    this.initMap();
  }

	private initData(data): any[] {
    return data.map(geodata => {
      const coor = geodata['geometry']['coordinates'];
      const poly = L.polygon(coor).addTo(this.map);
      // console.log( 'poly', poly );
      this.getNDVI(geodata['_id'] , poly['_bounds']);
      return geodata;
    });
	}
  
	public loadPolygons() {
		this.polygonService
			.getEvents()
			.subscribe((data: any[]) => {
				this.polygons.push( this.initData(data) );
			});
  }
  
  private initMap() {
    // this.el = this.elementRef.nativeElement;
    // const mapElem = this.el.querySelector('.map') as HTMLElement;
    // this.mapService.initMap(mapElem);
    this.map = L.map('map', {
      center: L.latLng(this.lng, this.lat),
      zoom: this.zoomDefault,
      minZoom: 3,
      maxZoom: 18,
      zoomControl: true,
      layers: [
        this.MapaHibrido
      ]
    });

    this.map.whenReady(() => {

      // L.tileLayer(tileUrl, {
      //     attribution: 'Google'
      // }).addTo(this.map);

      this.loadPolygons(); 

      var baseMaps = {
        "Híbrido": this.MapaHibrido,
        "Satélite ": this.MapaSatelite,
        "Terraneo": this.MapaTerranea,
        "Carretera": this.MapaCarretera,
    };
    L.control.layers(baseMaps, {}).addTo(this.map);

      this.map.panTo( [this.lat, this.lng] );
      this.map.locate({
        watch: false,
        setView: true,
        maxZoom: this.zoomDefault
      }).on('locationfound', (e) => {
        // console.log('locationfound' , e);
        const userIcon = L.icon({
          iconUrl: '/assets/images/icons/icon_person.png',
          iconSize: [70, 70],
          iconAnchor: [40, 79],
          popupAnchor: [0, -28]
        });
        L.marker(e.latlng, {icon: userIcon}).addTo(this.map);
        // this.map.panTo(e.latlng);
        this.map.panTo( [this.lat, this.lng] );
      }).on('locationerror', (err) => {
        console.error('locationerror' , err);
      });

    });

  }

  addPolygon() {

    this.deleteRiskPolygon();

    this.isCreating = true;
    this.polygonDrawer = new L.Draw.Polygon(this.map);
    const context = this;

    this.map.on('draw:created', (e: any) => {
      context.newRiskPolygon = e.layer;
      // context.newRiskPolygon.options.editing = {};
      // context.newRiskPolygon.editing.enable();
      context.newRiskPolygon.addTo(context.map);
    });

    this.polygonDrawer.enable();

  }

  deleteRiskPolygon() {

    if (this.newRiskPolygon) {
      this.newRiskPolygon.removeFrom(this.map);
    }
    if ( this.polygonDrawer ) {
      this.polygonDrawer.disable();
    }
    this.isCreating = false;
    this.map.off('draw:created');
  }

  private getNDVI( id , bounds ){
    this.httpService.ejecutarServicioGet( environment.Agromonitoring.ndvi + id ).subscribe( (res: Response)=> {
      // console.log( 'NDVI' , res, res.json());
      if (res.ok) {
        // console.log( 'NDVI' , res, res.json());
        L.imageOverlay(res.json()[0].image.ndvi, bounds).addTo(this.map);
        // CREATION ON
        this.newRiskPolygon = undefined;
        this.isCreating = false;
        this.loadingService.end();
      } else {
        //ERROR
        this.showToast('Error creando la capa NDVI');
        this.loadingService.end();
      }
    } );
  }
  createRiskPolygon(){
    if (this.newRiskPolygon) {
      // const center = this.newRiskPolygon.getBounds().getCenter();
      var body = {  "name" : "tmp" + this.makeid(10) , 
                    "geo_json" : this.newRiskPolygon.toGeoJSON() };

      // console.log( this.newRiskPolygon.toGeoJSON() );
      var area = turf.area( this.newRiskPolygon.toGeoJSON() ) / 10000;
      //console.log( 'area', area/10000 );

      
      if ( area < 1  ) {
        //AREA PASSED 
        this.showToast('El área mínima permitida es de 1 Hectarea');
        return;
      }

      if ( area > 3000  ) {
        //AREA PASSED 
        this.showToast('El área máxima permitida es de 3000 Hectareas');
      } else {

        this.loadingService.start();
        this.httpService.ejecutarServicioPost( environment.Agromonitoring.polygon, body ).subscribe( (res: Response)=> {
          
          if (res.ok) {
            this.getNDVI( res.json()['id'], this.newRiskPolygon.getBounds() );
          } else {
            //ERROR
            this.showToast('Error creando la capa NDVI');
            this.loadingService.end();
          }
        });
      }
      
      //this.isCreating = false;
    } else {
      this.showToast('Primero se debe crear un polígono');
    }
  }

  showToast( msg ) {
    this.toastr.clear();
    this.toastr.info( msg , 'Agriapp' , { timeOut: 3000 });
    // this._snackBar.open(msg, undefined, {duration: 5000} );
  }

  makeid(length) {
    var result           = '';
    var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for ( var i = 0; i < length; i++ ) {
       result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
 }

}
