import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { Headers, Http, Response, Request, RequestOptions, RequestOptionsArgs, XHRBackend } from '@angular/http';
import { HttpParams } from '@angular/common/http';
import { catchError, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class HttpService extends Http {

  constructor(backend: XHRBackend, options: RequestOptions, public http: Http) {
    super(backend, options);
  }

  public request(url: string | Request, options?: RequestOptionsArgs): Observable<Response> {
      return super.request(url, options).pipe(
        /*
        tap(  (res: Response) => { if ( res.status === 200 ) { 
          console.log(res);
          this.network.setOnline();
        } }  ) ,*/
        catchError(this.handleError)
    );
  }

  public handleError = (error: Response) => {
      if ( error.status === 401 ) {
        // this.router.navigate(['auth/login']);
      }
      return of (error);
  }

  private getToken() {
    return 0; // localStorage.getItem(environment.authTokenKey);
  }

  ejecutarServicioGet(url: string , params?: HttpParams ): Observable<Response> {

    return this.request(url, {
        search: params,
        method: 'get',
        headers: new Headers({
            'Content-Type': 'application/json; charset=UTF-8'/*,
            'Authorization': this.getToken()*/
        })
    });
  }

  ejecutarServicioGetCORS(url: string , params?: HttpParams ): Observable<Response> {

    return this.request(url, {
        search: params,
        method: 'get',
        headers: new Headers({
            'Content-Type': 'application/json; charset=UTF-8',
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept',
            'Access-Control-Allow-Methods': 'GET, POST, PUT, DELETE'/*,
            'Authorization': this.getToken()*/
        })
    });
  }

  ejecutarServicioPost(url: string, data?): Observable<Response> {
    console.log( url , data );
    return this.request(url, {
        body: JSON.stringify(data),
        method: 'post',
        headers: new Headers({
            'Content-Type': 'application/json; charset=UTF-8'/*,
            'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept',
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Methods': 'GET, POST, PUT, DELETE',
            'Authorization': this.getToken()*/
        })
    });
  }

  ejecutarServicioPostFormData(url: string, data?: FormData): Observable<Response> {
    return this.request(url, {
        body: data,
        method: 'post',
        headers: new Headers({
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept',
            'Access-Control-Allow-Methods': 'GET, POST, PUT, DELETE'/*,
            'Authorization': this.getToken()*/
        })
    });
  }

  ejecutarServicioPut(url: string, data?): Observable<Response> {
    return this.request(url, {
        body: JSON.stringify(data),
        method: 'put',
        headers: new Headers({
            'Content-Type': 'application/json; charset=UTF-8',
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept',
            'Access-Control-Allow-Methods': 'GET, POST, PUT, DELETE'/*,
            'Authorization': this.getToken()*/
        })
    });
  }

}
