import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { PolygonsDB } from 'src/app/shared/inmemory-db/polygons';

@Injectable({
  providedIn: 'root'
})
export class PolygonAppService {
  public events: any[];
  constructor(
    private http: HttpClient
  ) {}

  public getEvents(): Observable<any[]> {
    // return this.http.get('api/Polygon/events')
    // .map((events: PolygonEvent[]) => {
    //   this.events = events;
    //   return events;
    // });

    const eventDB = new PolygonsDB();
    return of(eventDB.my_polygons)
      .pipe(
        map(events => {
          this.events = events;
          return events;
        })
      );
  }

  public addEvent(event): Observable<any[]> {
    // return this.http.post('api/Polygon/events', event)
    // .map((events: any[]) => {
    //   this.events = events;
    //   return events;
    // });

    this.events.push(event);
    return of(this.events);
  }

  public updateEvent(event): Observable<any[]> {
    // return this.http.put('api/Polygon/events/'+event._id, event)
    // .map((events: any[]) => {
    //   this.events = events;
    //   return events;
    // });

    this.events = this.events.map(e => {
      if (e._id === event._id) {
        return Object.assign(e, event);
      }
      return e;
    });
    return of(this.events);
  }

  public deleteEvent(eventID: string): Observable<any[]> {
    // return this.http.delete('api/Polygon/events/'+eventID)
    // .map((events: any[]) => {
    //   this.events = events;
    //   return events;
    // });

    this.events = this.events.filter(e => e._id !== eventID);
    return of(this.events);
  }

}
